bado por una institución reconocida, que prevé, para un uso común y repetido, reglas, directrices o características para los productos o los procesos y métodos de producción conexos y cuya observancia no es obligatoria. También puede incluir prescripciones en materia de terminología, símbolos, embalaje, marcado o etiquetado aplicables a un producto, proceso o método de producción o tratar exclusivamente de ellas (Decreto 1074 de 2015. Ministerio de Comercio, Industria y Turismo, artículo 2.2.1.7.1.7., numeral 55, página 32).")])]),_c('div',{attrs:{"numero":"2","titulo":"Política pública"}},[_c('p',[_vm._v("Es un instrumento de planeación orientado a la acción para lograr objetivos prioritarios, fruto de un proceso de concertación intersectorial y co-creación, en el que participa la administración distrital, la ciudadanía, la sociedad civil, los gremios, la academia, entre otros. ")]),_c('p',[_vm._v("Las fases o etapas del ciclo de políticas públicas son:")]),_c('ul',{staticClass:"lista-ul ms-3 ms-md-4"},[_c('li',{staticClass:"mb-0"},[_c('span',[_vm._v("• Preparatorio.")])]),_c('li',{staticClass:"mb-0"},[_c('span',[_vm._v("• Agenda pública.")])]),_c('li',{staticClass:"mb-0"},[_c('span',[_vm._v("• Formulación.")])]),_c('li',{staticClass:"mb-0"},[_c('span',[_vm._v("• Implementación.")])]),_c('li',{staticClass:"mb-0"},[_c('span',[_vm._v("• Seguimiento.")])]),_c('li',{staticClass:"mb-0"},[_c('span',[_vm._v("• Evaluación. ")])])])]),_c('div',{attrs:{"numero":"3","titulo":"Reglamento técnico"}},[_c('p',[_vm._v("Es el documento en el que se establecen las características de un producto o los procesos y métodos de producción con ellas relacionadas, con inclusión de las disposiciones administrativas aplicables y cuya observancia es obligatoria. También puede incluir prescripciones en materia de terminología, símbolos, embalaje, marcado o etiquetado, aplicables a un producto, proceso o método de producción, o tratar exclusivamente de ellas. (Decreto 1074 de 2015. Ministerio de