ass:"col-md-8 mb-4 mb-md-0"},[_c('p',[_vm._v("Según la Encuesta de Calidad de Vida de 2019, en Colombia el 87,0 % de la población contaba con el servicio de acueducto y el 74,4 % con alcantarillado. El acceso a servicios fue mayor en el área urbana, que en el área rural. El 98,1 % de los hogares recibieron el servicio de energía eléctrica, el 64,5 % gas natural, el 81,8 % accedieron a la recolección de basuras y el 25,2 % de telefonía fija. De acuerdo con la misma fuente, Colombia incremento ese mismo año 0,4 puntos porcentuales (p.p.) la cobertura de energía eléctrica; 0,1 p.p. de gas natural, acueducto y recolección de basuras; y descendió en 0,3 p.p. en alcantarillado, mientras en telefonía fija fue de 4,0 p.p. Este aumento en la cobertura del acueducto influyó en el mejoramiento de la calidad de vida.")])]),_c('div',{staticClass:"col-md-4 col-8"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema1/tema1-20.png"),"alt":""}})])])]),_c('div',{staticClass:"row",attrs:{"numero":"2","titulo":"Numeral 2.5. Calidad del agua"}},[_c('div',{staticClass:"col-md-8 mb-4 mb-md-0"},[_c('p',[_vm._v("De acuerdo con lo reportado por el SIVICAP, en los años 2017 y 2018, bajo el número de departamentos con riesgo alto por el consumo del agua, de 5 en 2017 a 4 en 2018, en el último año Putumayo paso de riesgo alto a medio; mientras que Sucre y Caquetá pasaron de riesgo medio a bajo. Córdoba y Guaviare pasaron de sin riesgo a riesgo bajo y Risaralda y Vichada pasaron de riesgo bajo a medio. Persistieron en el 2018 con riesgo alto los departamentos de Caldas, Vaupés, Huila y Nariño. Mientras para 2019 es de resaltar que ninguno de los departamentos reporta su indicador en alto riesgo de salud por el consumo de agua.")])]),_c('div',{staticClass:"col-md-4 col-8"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema1/tema1-21.png"),"alt":""}})])])]),_c('div',{staticClass:"row",attrs:{"numero":"3","titulo":"2.14.1. Calidad del aire (material particulado, PM10)"}},[_c('div',{staticClass:"col-md-8 mb-4 mb-md-0"},[_c('p',[_vm._v("El promedio en la concentración de material particulado de 10 micras en un periodo de 7 años, el cual se calculó a partir de los registros anuales de las estaciones de monitoreo que tenían datos completos para 2012, 2016 y 2019. Se presenta también el referente normativo de 30 µg/m3 proyectado al año 2030 según Resolución 2254 de 2017. ")]),_c('p',[_vm._v("La estación con mayor PM10 promedio es la de Acopi, localizada en la zona industrial de Yumbo, seguida por la estación Carvajal Sevillana de Bogotá, la estación Medellín Alta en Antioquia, la estación Kennedy en Bogotá y la estación la Jagua Vía en el municipio la Jagua de Ibirico, César. La estación con menor promedio fue la estación en la Cuenca Río Risaralda en el municipio de la Virginia, Risaralda. ")])]),_c('div',{staticClass:"col-md-4 col-8"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema1/tema1-22.png"),"alt":""}})])])]),_c('div',{staticClass:"row",attrs:{"numero":"4","titulo":"Numeral 2.6.2. Inocuidad de los alimentos. Enfermedades Transmitidas por Alimentos (ETA)"}},[_c('div',{staticClass:"col-md-8 mb-4 mb-md-0"},[_c('p',[_vm._v("Estas enfermedades constituyen un importante problema de salud pública, de acuerdo con la OMS, afectaron a 600 millones de personas y causaron 420.000 muertes prematuras en el año 2010. La carga mundial de ETA es comparable con las principales enfermedades infecciosas, este tipo de enfermedades afecta con mayor frecuencia a niños menores de cinco años. Según un estudio del Banco Mundial en 2018, el impacto de los alimentos insalubres cuesta a las economías de ingreso bajo y mediano alrededor de USD 110.000 millones anuales en pérdida de productividad y gastos médicos. Por ende, una mejor gestión de la inocuidad de los alimentos contribuiría de manera significativa al logro de varios de los Objetivos de Desarrollo Sostenible, en particular aquellos relacionados con la pobreza, 